.intro-hero img{
    height: 180px;
    width: 180px;
  }

  .intro-hero-desk{
    width:270px;
    display: flex;
    height: 182px;
    flex-direction: column;
    justify-content: space-between;
  }
  .intro-hero-desk h3{
    font-size: 22px;
    margin:0; 
  }
  .intro-hero-desk p{
    font-size: 14px;
  }
  .intro-hero-desk-buttons{
    margin-top:7px;
    display: flex;
    justify-content: center;
    gap:30px;
  }
  .intro-hero-desk-buttons a{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
  }
  .intro-random{
    background-color: black;
  }
  .intro-random h3{
    margin: 0;
  }
  .intro-random-desk{
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 24px;
    height: 181px;
    font-weight: 700;
  }
  .intro-random-desk p {
    margin-top: 20px;
  }
  .intro-random-desk span{
    display: block;
  }
  .intro-random img {
    position: absolute;
    bottom:14px;
    right:-20px;
  }