.hero-list-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 0px 50px 0px;
}
.hero-list {
  /* margin-right:25px; */
  /* width: 690px; */
  /* height: 900px; */
  /* background-color: rgb(223, 255, 218); */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  margin-right: 20px;
  /* overflow: hidden; */
  padding: 0px 0px 20px 0px;
}
.hero-list button {
  justify-self: center;
}
.hero-item {
  cursor: pointer;
  width: 200px;
  height: 315px;
  background-color: rgba(0, 0, 0, 1);
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.16);
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  border-radius: 4px;
  overflow: hidden;
}
.hero-item:hover {
  box-shadow: 0px 0px 5px black;
  transform: translateY(-5px);
}

.hero-item img {
  width: 200px;
  height: 200px;
}
.hero-item-name {
  text-transform: uppercase;
  font-weight: 700;
  padding: 15px;
  color: white;
  font-size: 22px;
}
.hero-list-wrapper .btn {
  margin-top: 2vh;
  /* height: 25px !important; */
  transition: transform 0.5s ease-in-out, filter 0.5s ease-in-out;
}
.hero-list-wrapper .btn:disabled {
  filter: grayscale(80%);
}
.hero-list-wrapper .btn:hover {
  transform: scale(1.1);
}
